import { Component } from '@angular/core';
import { NavController, NavParams, ToastController, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';

@Component({
  selector: 'page-washing',
  templateUrl: 'washing.html',
})
export class WashingPage {

 washing: any;
 bedding: any;
 washPress: any;
 cartItems: any;

  constructor(public alert: AlertController, private authService: AuthServiceProvider, public navCtrl: NavController, public navParams: NavParams, public toastCtrl: ToastController, public storage: Storage) {
    this.getStorage();
  }

  getStorage(){
    this.storage.ready().then(()=>{
      this.storage.get("washingData").then((data) => {
        this.washing = data;
        console.log(this.washing)        
      })
      this.storage.get("beddingData").then((data) => {
        this.bedding = data;
        console.log(this.bedding)        
      })
      this.storage.get("washPressData").then((data) => {
        this.washPress = data;
        console.log(this.washPress)        
      })
    });
  }
  
  check(wash){
    this.storage.ready().then(()=>{
      for(let i = 0; i < this.washing.length; i++) {
        if(wash.id == this.washing[i].id) {
          this.storage.get("washingData").then((data) => {
            for(let j = 0; j < data.length; j++) {
                if(wash.id == data[j].id) {
                data[j].qty = wash.qty;
              }
              this.storage.set("washingData",data).then(()=>{
                this.washing = data;
              })
            }
          });
    }
    }       
  })
    console.log(wash.name,wash.qty,wash.amount);
    let item = {"name":wash.name,"amount": parseFloat(wash.amount)}
    if(wash.qty == true){
      this.cart(item);
    }else {
      this.removeFromCart(item)
    }

    this.storage.get("updateItem").then((data) => {
      data = [];
      if(wash.name == "Express Delivery"){
        data.push({
          "name" : "Express Delivery",
          "option" : "yes"
        })
      }
      this.storage.set("updateItem",data).then(()=>{
        console.log(data);
      })
    })
  }

  addItem(id) {
    this.storage.ready().then(()=>{
          for(let i = 0; i < this.bedding.length; i++) {
            if(id == this.bedding[i].id) {
              this.storage.get("beddingData").then((data) => {
                for(let j = 0; j < data.length; j++) {
                    if(id == data[j].id) {
                    let qty = data[j].qty;
                    data[j].qty = qty+1;
                  }
                  this.storage.set("beddingData",data).then(()=>{
                    this.bedding = data;
                  })
                }          
            this.cart(data[i]);
              });
        }
        }       
      })
      this.getStorage();
  }

  deleteItem(id){
    
    this.storage.ready().then(()=>{
      for(let i = 0; i < this.bedding.length; i++) {
        if(id == this.bedding[i].id) {
          this.storage.get("beddingData").then((data) => {
            for(let j = 0; j < data.length; j++) {
                if(id == data[j].id) {
                let qty = data[j].qty;
                data[j].qty = qty-1;
              }
              this.storage.set("beddingData",data).then(()=>{
                this.bedding = data;
              })
            }     
        this.removeFromCart(data[i]);
          });
    }
    }       
  })
  this.getStorage();
   
    }

    addItemPress(id) {
      this.storage.ready().then(()=>{
            for(let i = 0; i < this.washPress.length; i++) {
              if(id == this.washPress[i].id) {
                this.storage.get("washPressData").then((data) => {
                  for(let j = 0; j < data.length; j++) {
                      if(id == data[j].id) {
                      let qty = data[j].qty;
                      data[j].qty = qty+1;
                    }
                    this.storage.set("washPressData",data).then(()=>{
                      this.washPress = data;
                    })
                  }          
              this.cart(data[i]);
                });
          }
          }       
        })
        this.getStorage();
    }
  
    deleteItemPress(id){
      
      this.storage.ready().then(()=>{
        for(let i = 0; i < this.washPress.length; i++) {
          if(id == this.washPress[i].id) {
            this.storage.get("washPressData").then((data) => {
              for(let j = 0; j < data.length; j++) {
                  if(id == data[j].id) {
                  let qty = data[j].qty;
                  data[j].qty = qty-1;
                }
                this.storage.set("washPressData",data).then(()=>{
                  this.washPress = data;
                })
              }     
          this.removeFromCart(data[i]);
            });
      }
      }       
    })
    this.getStorage();
     
      }


  public cart(item){
    this.storage.get("cart").then((data) => {
      if(data == null || data.length ==0){
        data = [];
        data.push({
        "name": item.name,
        "qty": 1,
        "price": parseFloat(item.amount)
        });
        } else {
        let added = 0;
        for(let i = 0; i < data.length; i++) {
          if(item.name == data[i].name) {
          console.log("product added to cart");
          let qty = data[i].qty;
          data[i].qty = qty+1;
          data[i].price = parseFloat(data[i].price) + parseFloat(item.amount);
          added = 1;
          }	
        }
        if(added == 0) {
        data.push({
         "name": item.name,
         "qty": 1,
         "price": parseFloat(item.amount)
        });
        }
        }
        this.storage.set("cart",data).then(()=>{
        console.log(data);
        
        this.toastCtrl.create({
        message: "Cart Updated",
        duration: 3000
        }).present();	
        })
      });
      
      }

  removeFromCart(item){
    this.storage.get("cart").then((data) => {
      this.cartItems = data;
      if(data !== null || data.length !== 0){
        for(let i = 0; i < data.length; i++) {
          if(item.name == data[i].name) {
          
          let qty = data[i].qty;
          data[i].qty = qty-1;
          data[i].price = parseFloat(data[i].price) - parseFloat(item.amount);

          if(data[i].qty == 0){
            data.splice(i,1);
          }
          }	
        }
        }
        this.storage.set("cart",data).then(()=>{
        
        this.toastCtrl.create({
        message: "Cart Updated",
        duration: 3000
        }).present();	
        })
      });
  }

}



