import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';

@Component({
  selector: 'page-orders',
  templateUrl: 'orders.html',
})
export class OrdersPage {

  orderData: any;
  orderList: Array<String> = [];
  pickupData: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public authService: AuthServiceProvider) {
    this.orderData = {"user_id":"","token":""}
    const data = JSON.parse(localStorage.getItem('data'));
    this.orderData.user_id = data.userData.user_id;
    this.orderData.token = data.userData.token;

    this.authService.postData(this.orderData,"orderList").then((result) => {
      console.log(result);
      if(result){
      this.pickupData = result;
      }
      for(let i=0; i<this.pickupData.orderData.length;i++){
        this.orderList.push(this.pickupData.orderData[i]);
      }
      console.log(this.orderList);
      })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad OrdersPage');
  }

}



