import { HomePage } from './../home/home';
import { Validators, FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController, ToastController } from 'ionic-angular';
import { CreditCardValidator } from 'angular-cc-library';
import { Stripe } from '@ionic-native/stripe';
import { Http, Headers } from '@angular/http';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'page-payment',
  templateUrl: 'payment.html',
  providers:[Stripe]
})

export class PaymentPage  {

  public token;
  loading: any;
  public money:number=0;
  public receivedData: any;
  public message: any;

  customer: any = {}
  paymentPostData: any = {"user_id": "", "email": "", "customer_id": "", "token":""}

  public userDetails: any;

  public userPostData = {"user_id":"", "token":""};
  cardInfo: any;
  cardData: any;
  cardsList: Array<String> = [];

  item: string;

  public ccNumber: string
  public modelWithValue: string
  public formControlInput: FormControl = new FormControl()
  public mask: Array<string | RegExp>

  public expire: string
  public maskExpire: Array<string | RegExp>

  public cvv: string
  public maskCVV: Array<string | RegExp>

  public zipcode: string
  public maskZip: Array<string | RegExp>
  loader: any;

  constructor(public storage: Storage, public toastCtrl: ToastController, public authService: AuthServiceProvider, private stripe: Stripe, public alert: AlertController, public loadingCtrl: LoadingController, private http: Http, private _fb: FormBuilder, public navCtrl: NavController, public navParams: NavParams) {
    this.mask = [/\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]
    this.ccNumber = '';

    this.maskExpire = [/[0-1]/, /\d/, '/', /[1-2]/, /\d/, /\d/, /\d/]
    this.expire = '';

    this.maskCVV = [/[0-9]/, /\d/, /\d/, /\d/]
    this.cvv = '';

    this.maskZip = [/[0-9]/, /\d/, /\d/, /\d/,/\d/]
    this.zipcode = '';

    const data = JSON.parse(localStorage.getItem('data'));
    this.userDetails = data.userData;

    this.userPostData.user_id = this.userDetails.user_id;
    this.userPostData.token = this.userDetails.token;
    this.displayPayment();
  }

  displayPayment(){
    this.presentLoading();
    this.authService.postData(this.userPostData,"getPayment").then((result) => {
      this.receivedData = result;
      if(this.receivedData != null && this.receivedData.length != 0){
      this.cardInfo = result;
      for(let i=0; i<this.cardInfo.paymentData.length; i++){
      this.displayCards(this.cardInfo.paymentData[i].customer_id);
      }
    }else {
      this.message = "No Cards";
    }
  })
  setTimeout(() => {
    this.loader.dismiss();
  }, 5000);
  }

  displayCards(customerId){
    this.http.get('http://www.broadway-bubbles.com/stripe/getCardDetails.php?customer_id='+customerId).subscribe((res) => {
        this.cardData = res.json();
        console.log(this.cardData);
        this.cardsList.push(this.cardData);
        console.log(this.cardsList);
    })
  }

  save() {
    console.log(this.ccNumber.slice(0,4)+this.ccNumber.slice(5,9)+this.ccNumber.slice(10,14)+this.ccNumber.slice(15,19));
    console.log(this.expire.slice(0,2), this.expire.slice(3,7), this.cvv.replace('_',''), this.zipcode);

    //this.stripe.setPublishableKey('pk_live_DHwxcZn1QkjzG44dllKG4ViT');
    //.split('/r').join('/')
    let card = {
      number: this.ccNumber.slice(0,4)+this.ccNumber.slice(5,9)+this.ccNumber.slice(10,14)+this.ccNumber.slice(15,19),
      expMonth: parseInt(this.expire.slice(0,2)),
      expYear: parseInt(this.expire.slice(3,7)),
      cvc: this.cvv.replace('_',''),
      //address_zip: this.zipcode
  };
    
    this.stripe.setPublishableKey('pk_test_XywMCCW4OVp2ADoHC7zubqSz');
    this.stripe.createCardToken(card).then((token) => { 
    let data = JSON.stringify({"stripeToken": token.id, "email": this.userDetails.email});
    this.http.post('http://www.broadway-bubbles.com/stripe/signup.php/', data).subscribe((res) => {
      const response = res.json()
      if(response.status) {
        this.customer = response.data.customer;

        this.paymentPostData.user_id = this.userDetails.user_id;
        this.paymentPostData.email = this.userDetails.email;
        this.paymentPostData.token = this.userDetails.token;
        this.paymentPostData.customer_id = this.customer;
        this.authService.postData(this.paymentPostData,"payment").then((result) => {
                    
      },error => {
          let alert = this.alert.create({
              title: 'Warning',
              subTitle: 'There is an error! Please Try Again !',
              buttons: ['OK']
          });
          alert.present();
      });

        let alert = this.alert.create({
          title: 'Success',
          subTitle: 'Card has been added Successfully',
          buttons: ['OK']
      });
      alert.present();
        console.log({cus: this.customer})
      } else {
        alert('Failed:'+ response.message);
      }
    })
  })
  this.displayPayment();

   }

   getPayment(data){
    this.item = data;
    this.storage.get("pay").then((data)=>{
      data = [];
      data.push(this.item)
      this.storage.set("pay",data);
    })    
    this.toastCtrl.create({
      message: "Card Added",
      duration: 3000
      }).present();
      this.navCtrl.popTo(HomePage)
    
   }

   presentLoading() {
    
       this.loader = this.loadingCtrl.create({
         content: "Loading..!"
       });
    
       this.loader.present();
    
     }

}



