import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
//http://70.32.28.57/plesk-site-preview/www.broadway-bubbles.com/index.php/
//http://broadway-bubbles.com/PHP-Slim/api/index.php/
let apiUrl = "http://www.broadway-bubbles.com/PHP-Slim/api/index.php/";

@Injectable()
export class AuthServiceProvider {

  constructor(public http: Http) {
    //console.log('Hello AuthServiceProvider Provider');
  }

  postData(credentials, type){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return new Promise((resolve, reject) => {
      let headers = new Headers();
      this.http.post(apiUrl+type, JSON.stringify(credentials), {headers: headers}).subscribe(res =>{
        resolve(res.json()); 
      }, (err) => {
        reject(err);
      });
    });
  }

}



