import { Component, OnInit } from '@angular/core';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';
import { NavController, NavParams, ModalController, AlertController, LoadingController } from 'ionic-angular';
import { ModalAutocompleteItemsPage } from './../modal-autocomplete-items/modal-autocomplete-items';
import {Http} from "@angular/http";

declare var google:any;
@Component({
  selector: 'page-page-gmap-autocomplete',
  templateUrl: 'page-gmap-autocomplete.html',
})
export class PageGmapAutocompletePage implements OnInit{

  address:any = {
    place: '',
    set: false,
  };
  placesService:any;
  map: any;
  markers = [];
  placedetails: any;
  placedetailsData: any;

  public userDetails: any;

  constructor(public navCtrl: NavController, private http : Http, private loading : LoadingController, private alert :AlertController, public authService : AuthServiceProvider, public navParams: NavParams, public modalCtrl: ModalController) { 
    const data = JSON.parse(localStorage.getItem('data'));
    this.userDetails = data.userData;

}

  ngOnInit() {
    this.initMap();
    this.initPlacedetails();
  }

  showModal() {
    // reset 
    this.reset();
    // show modal|
    let modal = this.modalCtrl.create(ModalAutocompleteItemsPage);
    modal.onDidDismiss(data => {
        console.log('page > modal dismissed > data > ', data);
        if(data){
            this.address.place = data.description;
            // get details
            this.getPlaceDetail(data.place_id);
        }                
    })
    modal.present();
}

private reset() {
    this.initPlacedetails();
    this.address.place = '';
    this.address.set = false;
}

private getPlaceDetail(place_id:string):void {
    var self = this;
    var request = {
        placeId: place_id
    };
    this.placesService = new google.maps.places.PlacesService(this.map);
    this.placesService.getDetails(request, callback);
    function callback(place, status) {
        if (status == google.maps.places.PlacesServiceStatus.OK) {
            console.log('page > getPlaceDetail > place > ', place);
            // set full address
            self.placedetails.address = place.formatted_address;
            self.placedetails.lat = place.geometry.location.lat();
            self.placedetails.lng = place.geometry.location.lng();
            
            for (var i = 0; i < place.address_components.length; i++) {
                let addressType = place.address_components[i].types[0];
                let values = {
                    short_name: place.address_components[i]['short_name'],
                    long_name: place.address_components[i]['long_name']
                }
                if(self.placedetails.components[addressType]) {
                    self.placedetails.components[addressType].set = true;
                    self.placedetails.components[addressType].short = place.address_components[i]['short_name'];
                    self.placedetails.components[addressType].long = place.address_components[i]['long_name'];
                }                                     
            }                  
            // set place in map
            self.map.setCenter(place.geometry.location);
            self.createMapMarker(place);
            // populate
            self.address.set = true;
            console.log('page > getPlaceDetail > details > ', self.placedetails);
        }else{
            console.log('page > getPlaceDetail > status > ', status);
        }
    }
}

private initMap() {
    var point = {lat: 40.730610, lng: -73.935242}; 
    let divMap = (<HTMLInputElement>document.getElementById('map'));
    this.map = new google.maps.Map(divMap, {
        center: point,
        zoom: 15,
        disableDefaultUI: true,
        draggable: true,
        zoomControl: true
    });
}

private createMapMarker(place:any):void {
    var placeLoc = place.geometry.location;
    var marker = new google.maps.Marker({
      map: this.map,
      position: placeLoc
    });    
    this.markers.push(marker);
}

private initPlacedetails() {
    this.placedetails = {
        name:'',
        apt:'',
        token: '',
        user_id: '',
        address: "",
        lat: "",
        lng: "",
        
        components: {
            route: { set: false, short:'', long:'' },                           // calle 
            street_number: { set: false, short:'', long:'' },                   // numero
            sublocality_level_1: { set: false, short:'', long:'' },             // barrio
            locality: { set: false, short:'', long:'' },                        // localidad, ciudad
            administrative_area_level_2: { set: false, short:'', long:'' },     // zona/comuna/partido 
            administrative_area_level_1: { set: false, short:'', long:'' },     // estado/provincia 
            country: { set: false, short:'', long:'' },                         // pais
            postal_code: { set: false, short:'', long:'' },                     // codigo postal
            postal_code_suffix: { set: false, short:'', long:'' },              // codigo postal - sufijo
        } 
       
    };   
    this.placedetails.user_id = this.userDetails.user_id;
    this.placedetails.token = this.userDetails.token;
}    

saveAddress(){   
    console.log(this.placedetails);
    let zip = this.placedetails.components.postal_code.short;
    if( zip == 10023 || zip == 10024 || zip == 10025){
        let alert = this.alert.create({
            title: 'Add address name',
            inputs: [
                {
                  name: 'addressName', //data.addressName
                  placeholder: 'Address Name'
                },
                {
                    name: 'apt', //data.addressName
                    placeholder: 'Apartment Number'
                }
            ],
    
            buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
          },
          {
            text: 'Add',
            handler: data => {
    
                this.placedetails.name = data.addressName;
                this.placedetails.apt = data.apt;
    
                this.authService.postData(this.placedetails,"address").then((result) => {
                    //this.placedetailsData = result;
                    //localStorage.setItem('data',JSON.stringify(this.placedetailsData));
                    console.log(this.placedetails);
                    let alert = this.alert.create({
                        title: 'Success',
                        subTitle: 'Your Address Has been added.!',
                        buttons: ['OK']
                    });
                    alert.present();
                },error => {
                    let alert = this.alert.create({
                        title: 'Warning',
                        subTitle: 'There is an error! Please Try Again !',
                        buttons: ['OK']
                    });
                    alert.present();
                });          
            }
          }
        ]
        });
        alert.present();
    }else {
        let alert = this.alert.create({
            title: 'Sorry',
            subTitle: 'We do not have service in '+ zip + ' area',
            buttons: ['OK']
        });
        alert.present();
    }

      } 
}



